import React, { useState, useEffect } from 'react';
import * as XLSX from 'xlsx';
import '../UserLogs.css';


function UserLogs() {
    const [logs, setLogs] = useState([]);
    const [searchTerm, setSearchTerm] = useState('');
    const [selectedBatch, setSelectedBatch] = useState('');
    const [uniqueBatches, setUniqueBatches] = useState([]);
  
    useEffect(() => {
      const fetchLogs = () => {
        fetch('http://13.126.253.132:5000/api/userlogs')
          .then(response => response.json())
          .then(data => {
            const sortedData = data.sort((a, b) => a.batch_code.localeCompare(b.batch_code));
            setLogs(sortedData);
          })
          .catch(error => console.error('Error:', error));
      }
  
      fetchLogs();
      const intervalId = setInterval(fetchLogs, 5000);
  
      return () => clearInterval(intervalId);
    }, []);

  
  
    useEffect(() => {
      const batches = new Set(logs.map(log => log.batch_code));
      setUniqueBatches([...batches]);
    }, [logs]);
  
    if (logs.length === 0) {
      return null;
    }
  if (logs.length === 0) {
    return null;
  }
  const getColorClass = (columnName, value) => {
    if (
      columnName === 'last_played_position' ||
      columnName === 'last_played_position2' ||
      columnName === 'countdown_position' ||
      columnName === 'trial_position'
    ) {
      const numericValue = parseFloat(value);
      if (numericValue === 0) {
        return 'red-cell';
      } else if (numericValue > 0 && numericValue < 100) {
        return 'orange-cell';
      } else if (numericValue === 100) {
        return 'green-cell';
      }
    }
    return ''; // default class
  };

  

  let columnNames = Object.keys(logs[0]);
  columnNames = columnNames.filter(name => name !== 'user_id');
  columnNames.unshift('user_id');

  const filteredLogs = logs.filter(log =>
    (selectedBatch === '' || log.batch_code === selectedBatch) &&
    Object.values(log).some(value =>
      value !== null && value.toString().toLowerCase().includes(searchTerm.toLowerCase())
    )
  );
  

  const download = () => {
    const ws = XLSX.utils.json_to_sheet(filteredLogs);
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, "Logs");
    XLSX.writeFile(wb, "logs.xlsx");
  }

  
  return (
    <div className="userlogs-container">
    <button onClick={download} className="download-button">Download XLSX</button>
    <input
      type="text"
      placeholder="Search..."
      value={searchTerm}
      onChange={event => setSearchTerm(event.target.value)}
      className="search-input"
    />
    <select
      value={selectedBatch}
      onChange={(e) => setSelectedBatch(e.target.value)}
      className="batch-select"
    >
      <option value="">All Batches</option>
      {uniqueBatches.map((batch, index) => (
        <option key={index} value={batch}>
          {batch}
        </option>
      ))}
    </select>
      <table className="logs-table">
        <thead>
          <tr>
            {columnNames.map((columnName, index) => (
              <th key={index}>{columnName}</th>
            ))}
          </tr>
        </thead>
        <tbody>
          {filteredLogs.map((log, rowIndex) => (
            <tr key={rowIndex}>
              {columnNames.map((columnName, columnIndex) => (
                <td
                  key={columnIndex}
                  className={getColorClass(columnName, log[columnName])}
                >
                  {log[columnName]}
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

export default UserLogs;